<template>
  <div class="main">
    <el-button type="primary" class="addPublic" @click="add(-1)"><i class="el-icon-plus"></i> 添加自动回复</el-button>
    <el-table :data="autoReplay" border style="width: 100%;" v-loading="loading">
      <el-table-column type="index" width="60" label="ID">
      </el-table-column>
      <el-table-column prop="keyword" label="关键字" width="100">
      </el-table-column>
      <el-table-column prop="reply" label="回复内容" width="400">
        <template slot-scope="scope">
          <div v-if="scope.row.msgtype=='图文'">
            {{scope.row.remark}}
          </div>
          <div v-else>
            {{scope.row.reply}}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="eventtype" label="回复场景" width="110" :filters="[{ text: '关键字回复', value: '关键字回复' },{ text: '扫码回复', value: '扫码回复' }, { text: '欢迎语', value: '欢迎语' },{ text: '默认回复', value: '默认回复' }]" :filter-method="filterTag" filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag>{{scope.row.eventtype}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="msgtype" label="回复类型" width="80">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="success" size="middle" @click.stop="add(scope.row)"><i class="el-icon-edit"></i></el-button>
          <el-button type="danger" size="middle" @click.stop="del(scope.row)"><i class="el-icon-delete"></i></el-button>
          <!--二维码展示-->
          <el-button slot="reference" type="primary" size="middle" v-if="scope.row.eventtype=='扫码回复' " @click.stop="lookqrCode(scope.row)"><i class="el-icon-search"></i>二维码</el-button>
          <el-button slot="reference" type="primary" size="middle" v-if="scope.row.msgtype=='图片' " @click.stop="lookImgUrl(scope.row)"><i class="el-icon-search"></i>图片</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination @current-change="handleCurrentChange2" :current-page.sync="currentPage2" :page-size="10" layout="total, prev, pager,jumper, next" :total="total2" background>
    </el-pagination>
    <!--弹框-->
    <el-dialog :title="title" :visible.sync="dialogVisible" width="640px" top="8vh" :close-on-click-modal="false">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" status-icon>
        <el-form-item label="回复场景">
          <el-radio-group size="mini" v-model="replyRadio" @change="changeReplyRadio">
            <el-radio border label="1">关键字回复</el-radio>
            <el-radio border label="2">扫码回复</el-radio>
            <el-radio border label="3">欢迎语</el-radio>
            <el-radio border label="4">默认回复</el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="codeType==0">
          <div style="overflow: hidden;">
            <el-col :span="15">
              <el-form-item label="二维码类型" v-if="replyRadio==2">
                <el-radio-group size="mini" v-model="timeRadio">
                  <el-radio border label="1">永久二维码</el-radio>
                  <el-radio border label="2">临时二维码</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <div v-if="timeRadio==2 && replyRadio==2" style="width: 150px;margin-top:5px;">
                <el-tooltip class="item" effect="dark" content="临时二维码最长有效期30天" placement="bottom">
                  <input class="nativeInput" v-model="expire" @blur="changeVal"></input>
                </el-tooltip>
                <span>天后失效</span>
              </div>
            </el-col>
          </div>
          <el-form-item prop="keyword" label="关键字" v-if="replyRadio!=3 && replyRadio!=4">
            <el-input v-model="ruleForm.keyword"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <div style="overflow: hidden;">
            <el-col :span="15">
              <el-form-item label="二维码类型" v-if="replyRadio==2">
                <el-radio-group size="mini" v-model="timeRadio">
                  <el-radio border label="1" disabled>永久二维码</el-radio>
                  <el-radio border label="2" disabled>临时二维码</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <div v-if="timeRadio==2 && replyRadio==2" style="width: 150px;margin-top:5px;">
                <el-input size="small" v-model="expire" :disabled="true"></el-input>
              </div>
            </el-col>
          </div>
          <el-form-item prop="keyword" label="关键字" v-if="replyRadio!=3 && replyRadio!=4">
            <el-input v-model="ruleForm.keyword" :disabled="true"></el-input>
          </el-form-item>
        </div>
        <el-form-item label="回复类型">
          <el-radio-group size="mini" v-model="radio" @change="getRadio" v-if="addOrEdit==-1">
            <el-radio border label="1">文字</el-radio>
            <el-radio border label="2">图片</el-radio>
            <el-radio border label="3">图文</el-radio>
          </el-radio-group>
          <el-radio-group size="mini" v-model="radio" @change="getRadio" v-else>
            <div v-if="radio==1">
              <el-radio border label="1">文字</el-radio>
              <el-radio border label="2" :disabled="true">图片</el-radio>
              <el-radio border label="3" :disabled="true">图文</el-radio>
            </div>
            <div v-else-if="radio==2">
              <el-radio border label="1" :disabled="true">文字</el-radio>
              <el-radio border label="2">图片</el-radio>
              <el-radio border label="3" :disabled="true">图文</el-radio>
            </div>
            <div v-else-if="radio==3">
              <el-radio border label="1" :disabled="true">文字</el-radio>
              <el-radio border label="2" :disabled="true">图片</el-radio>
              <el-radio border label="3">图文</el-radio>
            </div>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="reply" label="回复内容" v-if="radio==1" key="1">
          <el-input type="textarea" :autosize="{ minRows: 6, maxRows: 12}" v-model="ruleForm.reply"></el-input>
        </el-form-item>
        <el-form-item label="回复图片" v-if="radio==2" key="2">
          <el-button type="success" @click="getForeverForLibrary" style="float:right;margin-top:10px">历史上传图片</el-button>
          <el-upload class="upload-demo" :action="uploadUrl" :multiple="false" :file-list="fileList" list-type="picture" :on-success="fileSuccess" :on-error="fileError" drag :on-remove="fileRemove">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          </el-upload>
        </el-form-item>
        <el-form-item label="选择图文列表" prop="imageText" v-if="radio==3" key="3">
          <el-autocomplete v-model="ruleForm.imageText" :fetch-suggestions="querySearch" placeholder="请输入内容" @select="handleSelect" style="width: 100%;">
            <template slot-scope="{ item }">
              <div class="title">{{ item.title }}</div>
            </template>
          </el-autocomplete>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button type="primary"  @click="submitForm('ruleForm')" v-loading="loading2">{{save}}</el-button>
        </span>
    </el-dialog>
    <!--显示二维码-->
    <el-dialog :title="qrCodeTitle" :visible.sync="dialogqrCode" width="280px">
      <img :src="qrCodeInfo.imgurl" style="width:200px;height: 200px; display: block; margin:0 auto;" />
      <span style="padding-right: 10px;" v-if="qrCodeInfo.codetype">{{qrCodeInfo.codetype}}</span>
      <span v-if="qrCodeInfo.expire>0 && qrCodeInfo.codetype" style="color:red;display: block; ">截止时间 :{{qrCodeInfo.endtime}}</span>
    </el-dialog>
    <!-- 显示图片信息 -->
    <el-dialog title="图片素材库" :visible.sync="dialogLibrary" width="700px" top="8vh" :close-on-click-modal="false">
      <div style="overflow:hidden">
        <img :src="item.local_imgurl" class="img " v-for="item in pageList" @click="chooseImg(item)" />
      </div>
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="20" layout="total, prev, pager,jumper, next" :total="total" style="margin-top:0;float:initial;text-align:right" background>
      </el-pagination>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      restaurants: [],
      autoReplay: [],
      loading: true,
      loading2: false,
      radio: "1",
      replyRadio: "1",
      timeRadio: "1",
      dialogVisible: false,
      dialogqrCode: false,
      dialogLibrary: false,
      currentPage: 1,
      pageList: null,
      total: 0,
      total2: 0,
      currentPage2: 1,
      qrCodeInfo: {},
      qrCodeTitle: '',
      save: "保 存",
      title: "",
      addOrEdit: -1,
      codeType: 0,
      expire: 30,
      fileList: [], //文件列表
      uploadUrl: process.env.API_HOST + this.tools.test + '/index/Wechat/uploadMatrial',
      media_id: null,
      newsData: null,
      ruleForm: {
        keyword: '',
        reply: '',
        imageText: ''
      },
      rules: {
        keyword: [{
          trigger: 'blur',
          required: true,
          message: '请输入关键字'
        }],
        reply: [{
          trigger: 'blur',
          required: true,
          message: '请输入回复内容'
        }],
        imageText: [{
          trigger: 'change',
          required: true,
          message: '请选择回复的图文'
        }]
      }
    }
  },
  watch: {
    expire: {
      handler: function(val) {
        var val = String(val)
        this.expire = val.replace(/[^\d]/g, '');
        if (val > 30) {
          this.expire = 30
        } else if (val < 0) {
          this.expire = 1
        }
      },
      deep: true

    }
  },
  methods: {
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.title.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    getRadio(val) {
      if (val == 3) {
        let data = {
          type: 'news',
          limit: 50,
          page: 1
        }
        this.getPage(data)
      }
    },
    handleSelect(item) {
      this.ruleForm.imageText = item.title;
      this.newsdata = item.newsdata
    },
    expandChange(row) {
      let index = this.expands.indexOf(row.id);
      if (index > -1) {
        this.expands.splice(index, 1);
      } else {
        this.expands.push(row.id)
      }
    },
    lookqrCode(row) {
      this.dialogqrCode = true;
      this.qrCodeInfo = {}
      this.qrCodeTitle = row.keyword + "对应的二维码";
      if (row.eventtype == "扫码回复") {
        if (row.expire == 0) {
          row.codetype = "永久二维码";
        } else {
          row.codetype = "临时二维码";
        }
      }
      this.qrCodeInfo = row
    },
    lookImgUrl(row) {
      this.qrCodeInfo = {}
      this.qrCodeTitle = "图片显示"
      let data = {
        media_id: row.reply
      }
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Imgmaterial/getImageUrl', data).then(res => {
        if (res.status == 1) {
          this.dialogqrCode = true
          this.qrCodeInfo = {
            imgurl: res.url,
            codetype: null
          }
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg);
        }
      });

    },
    changeVal() {
      if (this.expire == '') {
        this.expire = 30
      }
    },
    changeReplyRadio(val) {
      if (val == 2) {
        this.save = "生成二维码";
      } else {
        this.save = "保 存";
      }
      if (this.addOrEdit != -1) {
        if (val == 1) {
          this.codeType = 0
        } else {
          this.codeType = 1
        }

      }
    },
    getReplyInfo(val) {
      let data = {
        page: val,
        limit: 10
      }
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/index.html', data).then(res => {
        this.loading = false
        if (res.status == 1) {
          this.total2 = res.count
          res.replylist.forEach((item, index) => {
            if (item.msgtype == "text") {
              item.msgtype = "文字"
            } else if (item.msgtype == "image") {
              item.msgtype = "图片"
            } else if (item.msgtype == "news") {
              item.msgtype = "图文"
            }
            if (item.eventtype == 1) {
              if (item.keyword == "DEFAULT_REPLY") {
                item.eventtype = "默认回复"
              } else {
                item.eventtype = "关键字回复"
              }
            } else if (item.eventtype == 13) {
              item.eventtype = "扫码回复"
            } else if (item.eventtype == 11) {
              item.eventtype = "欢迎语"
            }
          })
          this.autoReplay = res.replylist

        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    filterTag(value, row) {
      return row.eventtype === value;
    },
    del(row) {
      this.$confirm('是否删除 ' + row.keyword + ' 这个关键字')
        .then(_ => {
          let data = {
            id: row.id
          }
          this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/delReply.html', data).then(res => {
            if (res.status == 1) {
              this.$message.success(res.msg);
              this.getReplyInfo(1);
            } else if (res.status == 2) {
              this.$router.push({
                name: 'login',
                path: '/login'
              });
            } else {
              this.$message.error(res.msg);
            }
          });
        }).catch(_ => {});
    },
    add(row) {
      this.fileList = []
      this.media_id = null
      this.dialogVisible = true
      if (row == -1) {
        this.addOrEdit = -1; //新增
        this.title = "添加自动回复";
        this.codeType = 0;
        this.ruleForm = {
          keyword: '',
          reply: ''
        };
        this.expire = 30;
        this.replyRadio = "1";
        this.timeRadio = "1";
        this.radio = "1";
        this.save = "保 存";
      } else {
        this.addOrEdit = row.id; //新增
        this.title = "编辑自动回复";
        let data = {
          id: row.id
        };
        this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/getReplyInfo.html', data).then(res => {
          if (res.status == 1) {
            if (res.replyinfo.msgtype == "text") {
              this.radio = "1"
            } else if (res.replyinfo.msgtype == "image") {
              this.radio = "2"
              this.fileList = [{ name: '图片', url: res.replyinfo.local_imgurl }]
              this.media_id = res.replyinfo.reply
            } else if (res.replyinfo.msgtype == "news") {
              this.ruleForm = {
                keyword: res.replyinfo.keyword,
                reply: res.replyinfo.reply,
                imageText: res.replyinfo.remark
              }
              this.newsdata = res.replyinfo.reply
              this.radio = "3"
              let data = {
                type: 'news',
                limit: 50,
                page: 1
              }
              this.getPage(data)
            }
            this.codeType = 0;
            if (res.replyinfo.eventtype == 1) {
              if (res.replyinfo.keyword == "DEFAULT_REPLY") {
                this.replyRadio = "4"
              } else {
                this.replyRadio = "1"
              }
            } else if (res.replyinfo.eventtype == 13) {
              if (res.replyinfo.qrtype == 1) {
                this.timeRadio = "2"
                this.expire = res.replyinfo.expire
              } else {
                this.timeRadio = "1"
                this.expire = 0
              }
              this.replyRadio = "2"
              this.codeType = 1;
            } else if (res.replyinfo.eventtype == 11) {
              this.replyRadio = "3"
            }
            if (res.replyinfo.msgtype != "news") {
              this.ruleForm = res.replyinfo
            }
          } else if (res.status == 2) {
            this.$router.push({
              name: 'login',
              path: '/login'
            });
          } else {
            this.$message.error(res.msg);
          }
        });
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false;
        } else if (this.radio == 2 && this.media_id == null) {
          this.$message.error('请选取回复图片');
        } else {
          this.loading2 = true
          var data = {
            keyword: this.ruleForm.keyword,
            status: 1
          }
          //回复类型
          if (this.radio == 1) {
            data.msgtype = "text"
            data.reply = this.ruleForm.reply
          } else if (this.radio == 2) {
            data.msgtype = "image"
            data.reply = this.media_id
          } else if (this.radio == 3) {
            data.msgtype = "news"
            data.reply = this.newsdata
            data.remark = this.ruleForm.imageText
          }
          //回复场景
          if (this.replyRadio == 1) {
            data.eventtype = 1
          } else if (this.replyRadio == 2) {
            data.eventtype = 13
            //二维码类型
            if (this.timeRadio == 1) {
              data.expire = 0
              data.qrtype = 2
            } else if (this.timeRadio == 2) {
              data.expire = this.expire
              data.qrtype = 1
            }
          } else if (this.replyRadio == 3) {
            data.eventtype = 11
          } else if (this.replyRadio == 4) {
            data.eventtype = 1
            data.keyword = "DEFAULT_REPLY"
          }

          if (this.addOrEdit == -1) { //新增
            if (this.replyRadio == 2) { //选择二维码，要先生成二维码
              var data2 = {
                keyword: this.ruleForm.keyword
              };
              if (this.timeRadio == 1) {
                data2.expire = 0
                data2.qrtype = 2
              } else if (this.timeRadio == 2) {
                data2.expire = this.expire
                data2.qrtype = 1
              }
              this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Wechat/createQRcode.html', data2).then(res => {
                if (res.status == 1) {
                  data.imgurl = res.imgurl;
                  this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/addReply.html', data).then(res => {
                    this.loading2 = false
                    if (res.status == 1) {
                      this.$message.success(res.msg);
                      this.getReplyInfo(1);
                      this.$refs[formName].resetFields();
                      this.dialogVisible = false;
                      // 显示二维码
                      this.qrCodeTitle = data.keyword + "对应的二维码";
                      this.dialogqrCode = true;
                      if (data.eventtype == 13) {
                        if (data.expire == 0) {
                          data.codetype = "永久二维码";
                        } else {
                          data.codetype = "临时二维码";
                          data.endtime = res.info.endtime;
                        }
                      }
                      this.qrCodeInfo = data
                    } else if (res.status == 2) {
                      this.$router.push({
                        name: 'login',
                        path: '/login'
                      });
                    } else {
                      this.$message.error(res.msg);
                    }
                  });
                } else if (res.status == 2) {
                  this.$router.push({
                    name: 'login',
                    path: '/login'
                  });
                } else {
                  this.$message.error(res.msg);
                }
              });
            } else { //选择非二维码
              this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/addReply.html', data).then(res => {
                this.loading2 = false
                if (res.status == 1) {
                  this.$message.success(res.msg);
                  this.getReplyInfo(1);
                  this.$refs[formName].resetFields();
                  this.dialogVisible = false;
                } else if (res.status == 2) {
                  this.$router.push({
                    name: 'login',
                    path: '/login'
                  });
                } else {
                  this.$message.error(res.msg);
                }
              });
            }
          } else { //编辑
            data.id = this.addOrEdit;
            if (this.replyRadio == 2) {
              if (this.timeRadio == 1) {
                data.expire = 0
                data.qrtype = 2
              } else if (this.timeRadio == 2) {
                data.expire = this.expire
                data.qrtype = 1
              }
            }
            this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Autoreply/editReply.html', data).then(res => {
              this.loading2 = false
              if (res.status == 1) {
                this.$message.success(res.msg);
                this.getReplyInfo(1);
                this.$refs[formName].resetFields();
                this.dialogVisible = false;
              } else if (res.status == 2) {
                this.$router.push({
                  name: 'login',
                  path: '/login'
                });
              } else {
                this.$message.error(res.msg);
              }
            });

          }
        }
      })
    },
     //分页
    handleCurrentChange(val) {
      let data = {
        limit: 20,
        page: val
      }
      this.getAllImg(data)
    },
    handleCurrentChange2(val) {
      this.getReplyInfo(val)
    },
    // 图片上传成功
    fileSuccess(response, file, fileList) {
      if (response.status == 0) {
        this.$message.error(response.msg);
      } else {
        this.fileList = [{ name: file.name, url: file.url }]
        this.media_id = response.info.media_id
      }
    },
    // 图片上传失败
    fileError(response, file, fileList) {
      this.$message.error('图片上传失败,请重试');
    },
    // 取消
    fileRemove(file, fileList){
      this.fileList = []
      this.media_id = null
    },
    getForeverForLibrary() {
      this.dialogLibrary = true
      let data = {
        limit: 20,
        page: 1
      }
      this.getAllImg(data)
    },
    getAllImg(data) {
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Imgmaterial/getAllImg', data).then(res => {
        if (res.status == 1) {
          this.total = res.count
          this.pageList = res.list
          if (res.list.length == 0) {
            this.$message.error('图片库暂无素材~');
          }
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error('图发太多了，微信不让，明天再斗吧');
        }
      });
    },
    // 获取图文列表
    getPage(data) {
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Wechat/getForeverForLibrary', data).then(res => {
        if (res.status == 1) {
          this.restaurants = res.list
          if (res.list.length == 0) {
            this.$message.error('还没有发表过图文消息哦~');
          }
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 选择图片
    chooseImg(item) {
      this.dialogLibrary = false
      this.fileList = [{ name: '图片', url: item.local_imgurl }]
      this.media_id = item.media_id
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.$emit('activeIndex', 'autoReply');
      this.getReplyInfo(1);
      if (sessionStorage.getItem("name")) {
        this.$emit('nowPublic', sessionStorage.getItem("name"));
      } else {
        this.$emit('nowPublic', '暂时没有哦');
      }
    });
  },
}

</script>
<style scoped>
.addPublic {
  margin-bottom: 20px;
}

.nativeInput {
  width: 48px;
  height: 24px;
  border-radius: 4px;
  background: none;
  border: 1px solid #409EFF;
  text-align: center;
  margin-top: 2px
}

.el-pagination {
  float: right;
  margin-top: 20px;
}

.upload-demo {
  margin-top: 10px;
}

.img {
  width: 118px;
  height: 118px;
  float: left;
  margin: 6px;
  border: 1px solid transparent
}

.main{
  padding:20px;
}

</style>



