import axios from 'axios'  //axios
//var test="/Wechatadmin/WechatDevApi/public/index.php";  //正式
//var test="/jiansheng/WechatDevApi/public/index.php";   //IP访问
 var test="";   //IP访问
export default {
	getAxio(url,data){
		return axios({
				url: url,
				method: 'post',
				data: data
			})
			.then(res => {	
				return res.data;
			})
	},
	postAxio(url){
		return axios({
				url: url,
				method: 'get'
			})
			.then(res => {	
				return res.data;
			})
	},
	test:test
}


