<template>
  <div class="main">
    <div class="customerService">
      <div class="left" style="width:210px">
        <el-scrollbar class="scrollbar">
          <div>
            <div class="item" v-for="item in userInfoItem" :class="{active:item.flag}" @click="chooseUser(item)">
              <el-badge is-dot v-if="item.status!=1">
                <img :src="item.headimgurl" class="imgurl">
              </el-badge>
              <img :src="item.headimgurl" class="imgurl" v-else>
              <div class="item-center">
                <div class="name">{{item.nickname}}</div>
                <div class="lastquestion">{{item.content}}</div>
              </div>
              <div class="item_right">
                <span>{{item.time}}</span>
                <img src="static/image/label.png">
              </div>
            </div>
            <div v-if="userInfoItem.length==0" class="noUser">
              暂时没有用户列表哦
            </div>
          </div>
        </el-scrollbar>
      </div>
      <el-col :span="18" class="center">
        <div class="center_center">
          <div class="record">
            <el-scrollbar style="height:100%;" ref="elscrollbar">
              <div style="padding:30px;">
                <div v-for="item in DialogInfo" style="margin-bottom:20px;">
                  <div class="left_Dialog" v-if="item.replytype==1">
                    <img :src="item.headimgurl" class="img" @click="showUserInfo(item.openid)">
                    <div class="send" v-if="item.msgtype=='text'">
                      <div class="arrow"></div>
                      <pre>{{item.content}}</pre>
                    </div>
                    <div class="img2_wrap" v-else>
                      <img :src="item.picurl" class="img2" @click="showBigImg(item.picurl)">
                    </div>
                  </div>
                  <div class="right_Dialog" v-else>
                    <img src="static/image/logo.jpg" class="img">
                    <div class="send" v-if="item.msgtype=='text'">
                      <div class="arrow"></div>
                      <pre>{{item.content}}</pre>
                    </div>
                    <div class="img2_wrap" v-else>
                      <img :src="item.picurl" class="img2" @click="showBigImg(item.picurl)">
                    </div>
                  </div>
                  <!-- <div class="time_Dialog" v-else>
                    <span>{{item.time}}</span>
                  </div> -->
                </div>
              </div>
            </el-scrollbar>
            <!-- 打标签显示内容 -->
            <div class="label_wrap" v-if="label_wrap">
              <el-cascader :options="options" v-model="selectedOptions" @change="handleChange" :filterable="true" :class="{active1 :cascaderType}" class="cascader" expand-trigger="hover">
              </el-cascader>
              <div :class="{active2 :!cascaderType}" class="cascaderTwo">
                <img src="static/image/back.png" @click="goBack">
                <p>{{cascaderText}}</p>
                <el-input type="textarea" v-model="desc" :autosize="{ minRows: 6, maxRows: 6}" placeholder="可简述用户问题(选填)"></el-input>
                <el-button type="primary" class="label_add" @click="submitLabel">完成</el-button>
              </div>
            </div>
            <!-- link 显示内容 -->
            <div class="link_wrap" v-if="link_wrap">
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" :inline="true" status-icon>
                <el-form-item label="文字" prop="content">
                  <el-input v-model="ruleForm.content"></el-input>
                </el-form-item>
                <el-form-item label="链接" prop="link">
                  <el-input v-model="ruleForm.link"></el-input>
                </el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">立即生成</el-button>
              </el-form>
            </div>
          </div>
        </div>
        <div class="center_bottom">
          <div class="toolsBar">
            <img src="static/image/link.png" class="link" @click="addlink">
            <img src="static/image/image.png" class="image" @click="addPic">
            <img :src="addlabelType ? 'static/image/label.png':'static/image/label2.png'" class="label" @click="addlabel">
          </div>
          <div class="textarea_wrap">
            <textarea autofocus v-model="textareaContent" @input="changeTextareaContent"></textarea>
            <el-button size="small" class="button" type="success" @click="submit" :loading="fsType">发送</el-button>
            <div class="tip_wrap" v-if="tip_wrap">
              <img src="static/image/tip.png" class="tip_img">
              <span class="tip_text">{{tip_text}}</span>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="6" class="search">
        <div class="inputTelBefore" v-if="inputTelType">
          <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" status-icon>
            <el-form-item label="" prop="InputUserTel">
              <el-input v-model="ruleForm2.InputUserTel" type="tel" maxlength="11" placeholder="请输入用户手机号"></el-input>
            </el-form-item>
            <el-button type="primary" icon="el-icon-search" class="btn" @click="searchTel('ruleForm2')">搜索</el-button>
          </el-form>
        </div>
        <div v-else class="inputTelAfter">
          <el-scrollbar class="scroll">
            <div @mouseleave="detailBoxhide">
              <div v-for="item in userRecord">
                <div class="userRecord_item" @mouseenter="detailBoxShow">
                  <span class="time">{{item.time}}</span>
                  <span class="record">{{item.record}}</span>
                </div>
              </div>
            </div>
          </el-scrollbar>
          <!-- 订单详情 -->
          <div class="detailBox" v-if="detailBox">
          </div>
        </div>
      </el-col>
    </div>
    <!--显示大图-->
    <el-dialog :visible.sync="dialogbigImg">
      <img :src="imgurl" style="height:400px; margin:0 auto; display:block" />
    </el-dialog>
    <!-- 上传图片 -->
    <el-dialog :visible.sync="uploadImg">
      <el-button type="success" @click="getForeverForLibrary" style="float:right;margin-top:10px">历史上传图片</el-button>
      <el-upload class="upload-demo" drag :action='uploadUrl' :on-success="fileSuccess" :on-error="fileError" :file-list="fileList" list-type="picture" :on-remove="fileRemove">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击选取文件</em></div>
      </el-upload>
      <el-button style="margin-top:10px;" size="small" type="primary" @click="submitUpload">回复图片</el-button>
    </el-dialog>
    <!-- 显示图片信息 -->
    <el-dialog title="图片素材库" :visible.sync="dialogLibrary" width="700px" top="8vh" :close-on-click-modal="false">
      <div style="overflow:hidden">
        <img :src="item.local_imgurl" class="img2" v-for="item in pageList" @click="chooseImg(item)" />
      </div>
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="20" layout="total, prev, pager,jumper, next" :total="total" style="margin-top:0;float:initial;text-align:right" background>
      </el-pagination>
    </el-dialog>
    <!-- 显示用户信息 -->
    <el-dialog :visible.sync="userInfoDialog" width="340px" :title="userInfo.nickname" :show-close="false">
      <div class="userItem">
        手机号&nbsp;
        <input v-model="userInfo.telephone" value="userInfo.telephone" type="tel" maxlength="11" class="specInput" v-if="userInfo.telephone" @focus="inputFocus" />
        <input v-model="userInfo.telephone" value="userInfo.telephone" type="tel" maxlength="11" class="specInput" v-else placeholder="请添加用户手机号" @focus="inputFocus" /><span v-if="btnShow" class="userSave">保存</span>
        <img :src="userInfo.headimgurl" class="headimgurl" />
      </div>
      <div class="userItem" style="margin-bottom:15px;">
        地区&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{userInfo.country}} {{userInfo.province}} {{userInfo.city}}
      </div>
      <hr/>
      <div>
        <div v-for="item in UserTagInfo">
          <div style="overflow:hidden"><span style="color:#000; font-size:40px;float:left;border-radius:50%">·</span><span style="color:#000;">{{item.tagidstr}}</span><span style="float:right; font-size:12px;">上周四</span></div>
          <div><span style="font-size:13px;margin-left:14px;">{{item.remark}}</span></div>
        </div>
      </div>
      <el-pagination small layout="prev, pager, next" :total="tagTotal" style="float:right" @current-change="handleCurrentChange2" :current-page.sync="currentPage2" :page-size="3">
      </el-pagination>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    var validateTel = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'));
      } else if (!(/^1[3|4|5|6|8|7|9]\d{9}$/).test(value)) {
        callback(new Error('请输入正确格式的手机号'));
      } else {
        callback();
      }
    };
    return {
      detailBox: false,
      inputTelType: false,
      userRecord: [{ time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }, { time: '6-14 11:00', record: '购买了XX商品' }],
      btnShow: false, //保存按钮
      uploadUrl: process.env.API_HOST + this.tools.test + '/index/Wechat/uploadMatrial', // 图片上传路径
      cascaderType: true, // 标签内容左右滑动
      desc: '',
      fileList: [],
      fsType: false,
      dialogbigImg: false,
      uploadImg: false,
      dialogLibrary: false,
      currentPage: 1,
      imgurl: null,
      userInfo: {},
      userInfoItem: [],
      userInfoDialog: false,
      UserTagInfo: [],
      tagTotal: 0,
      DialogInfo: [],
      textareaContent: '',
      link_wrap: false,
      label_wrap: false,
      addlabelType: false,
      tip_wrap: false,
      tip_content: '',
      globalOpenid: null,
      currentPage2: 1,
      ruleForm: {
        content: '',
        link: ''
      },
      ruleForm2: {
        InputUserTel: ''
      },
      rules: {
        content: [
          { required: true, message: '请输入文字内容', trigger: 'blur' }
        ],
        link: [
          { required: true, message: '请输入链接接内容', trigger: 'blur' }
        ]
      },
      rules2: {
        InputUserTel: [
          { validator: validateTel, trigger: 'blur' }
        ]
      },
      media_id: '',
      total: 0,
      pageList: [],
      fileUrl: '',
      selectedOptions: [],
      options: [],
      cascaderText: ''

    }
  },
  methods: {
    detailBoxShow() {
      console.log(444)
       this.detailBox=true
    },
    detailBoxhide() {
      this.detailBox=false
    },

    //搜索手机号
    searchTel(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 搜索
        }
      });
    },
    inputFocus() {
      this.btnShow = true
    },
    handleChange(value) {
      this.cascaderText = ''
      this.options.forEach((item2) => {
        if (value[0] == item2.value) {
          this.cascaderText += item2.label
          if (value[1]) {
            this.cascaderText += '/'
            item2.children.forEach((item3) => {
              if (value[1] == item3.value) {
                this.cascaderText += item3.label
                if (value[2]) {
                  this.cascaderText += '/'
                  item3.children.forEach((item4) => {
                    if (value[2] == item4.value) {
                      this.cascaderText += item4.label
                    }
                  })
                }
              }
            })
          }
        }
      })
      this.cascaderType = false
    },
    handleCurrentChange2(val) {
      this.getUserTagInfo(this.globalOpenid, val, 3)

    },
    // 查看用户信息
    showUserInfo(openid) {
      this.userInfoDialog = true
      this.btnShow = false
      this.getCustomerUserInfo(openid)
      this.getUserTagInfo(openid, 1, 3)
    },
    goBack() {
      this.cascaderType = true
    },
    submitLabel() {
      let tagStr = this.selectedOptions.join(",")
      let data = {
        tagid: tagStr,
        openid: this.globalOpenid,
        remark: this.desc
      }
      this.addTag(data)
    },
    getForeverForLibrary() {
      this.dialogLibrary = true
      let data = {
        limit: 20,
        page: 1
      }
      this.getAllImg(data)
    },
    // 选择图片
    chooseImg(item) {
      this.dialogLibrary = false
      this.fileList = [{ name: '图片', url: item.local_imgurl }]
      this.media_id = item.media_id
      this.fileUrl = item.local_imgurl
    },
    //分页
    handleCurrentChange(val) {
      let data = {
        limit: 20,
        page: val
      }
      this.getAllImg(data)
    },
    //  点击上传
    submitUpload() {
      if (this.media_id == '') {
        this.$message.error('请选取回复图片');
      } else {
        let data = {
          content: '图片',
          openid: this.globalOpenid,
          msgtype: 'image',
          media_id: this.media_id,
          url: this.fileUrl
        }
        this.postInitReply(data, 'image')
      }
    },
    // 图片上传成功
    fileSuccess(response, file, fileList) {
      if (response.status == 0) {
        this.$message.error(response.msg);
      } else {
        this.fileList = [{ name: file.name, url: file.url }]
        this.media_id = response.info.media_id
        this.fileUrl = response.info.url
      }
    },
    // 图片上传失败
    fileError(response, file, fileList) {
      this.$message.error('图片上传失败,请重试');
    },
    fileRemove(file, fileList) {
      this.fileList = []
      this.media_id = null
    },
    // 点击用户
    chooseUser(item) {
      this.userInfoItem.forEach((item, index) => {
        item.flag = false

      })
      item.flag = true
      item.status = 1
      this.getUserChatInfo(item.openid, item.headimgurl)
    },
    // 查看大图
    showBigImg(imgUrl) {
      this.imgurl = imgUrl
      this.dialogbigImg = true
    },
    changeTextareaContent() {
      if (this.textareaContent.trim()) {
        this.tip_wrap = false
      }
    },
    // 提交数据
    submit() {
      if (!this.textareaContent.trim()) {
        this.tip_text = '不能发送空白信息'
        this.tip_wrap = true
      } else {
        let data = {
          content: this.textareaContent,
          openid: this.globalOpenid
        }
        this.postInitReply(data, 'text')
      }
    },
    // 添加链接
    addlink() {
      this.label_wrap = false
      this.addlabelType = false
      if (this.link_wrap) {
        this.link_wrap = false
      } else {
        this.link_wrap = true

      }
    },
    addlabel() {
      this.link_wrap = false
      if (this.label_wrap) {
        this.label_wrap = false
        this.addlabelType = false
      } else {
        this.label_wrap = true
        this.addlabelType = true
        this.getTag()
      }
    },
    // 添加图片
    addPic() {
      this.uploadImg = true
    },
    // 用户信息
    getCustomerUserInfo(openid) {
      this.tools.postAxio(process.env.API_HOST + this.tools.test + '/index/Customerinfo/getCustomerUserInfo?openid=' + openid).then(res => {
        if (res.status == 1) {
          this.userInfo = res.data
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 标签内容
    getUserTagInfo(openid, page, limit) {
      let data = {
        openid: openid,
        page: page,
        limit: limit
      }
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Customerinfo/getUserTagInfo', data).then(res => {
        if (res.status == 1) {
          this.UserTagInfo = res.data.user_tag_info
          this.tagTotal = res.data.count
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 增加标签
    addTag(data) {
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Tag/addTag', data).then(res => {
        if (res.status == 1) {
          this.cascaderType = true
          this.label_wrap = false
          this.addlabelType = false
          this.selectedOptions = []
          this.desc = ''
          this.$message.success(res.msg)
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 获取标签
    getTag() {
      this.tools.postAxio(process.env.API_HOST + this.tools.test + '/index/Tag/getTag').then(res => {
        if (res.status == 1) {
          this.options = res.data
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 获取聊天列表
    getChatList() {
      this.tools.postAxio(process.env.API_HOST + this.tools.test + '/index/Kefu/getChatList').then(res => {
        if (res.status == 1) {
          res.data.forEach((item, index) => {
            item.flag = false
          })
          res.data[0].flag = true
          res.data[0].status = 1
          this.getUserChatInfo(res.data[0].openid, res.data[0].headimgurl)
          this.userInfoItem = res.data
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 某个用户的聊天信息
    getUserChatInfo(openid, headimgurl) {
      this.globalOpenid = openid
      this.tools.postAxio(process.env.API_HOST + this.tools.test + '/index/Kefu/getUserChatInfo?openid=' + openid).then(res => {
        if (res.status == 1) {
          res.data.forEach((item, index) => {
            item.headimgurl = headimgurl
          })
          this.DialogInfo = res.data
          this.scrollBottom()
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    // 回复内容
    postInitReply(data, type) {
      this.fsType = true
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Wechat/initreply', data).then(res => {
        this.fsType = false
        if (res.status == 1) {
          if (type == 'text') {
            let obj = { replytype: 2, msgtype: 'text', content: this.textareaContent }
            this.DialogInfo.push(obj)
            this.textareaContent = ''
          } else {
            let obj = { replytype: 2, msgtype: 'image', picurl: data.url }
            this.DialogInfo.push(obj)
            this.fileList = []
            this.uploadImg = false
            this.media_id = ''
            this.fileUrl = ''
          }
          this.scrollBottom()
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    scrollBottom() {
      setTimeout(() => {
        let elscrollbar = this.$refs['elscrollbar'].$refs['wrap']
        elscrollbar.scrollTop = elscrollbar.scrollHeight
      }, 50)
    },
    // 提交链接
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let Alink = '<a href="' + this.ruleForm.link + '">' + this.ruleForm.content + '</a>'
          this.textareaContent += Alink
          this.$refs[formName].resetFields()
          this.link_wrap = false
        }
      });
    },
    getAllImg(data) {
      this.tools.getAxio(process.env.API_HOST + this.tools.test + '/index/Imgmaterial/getAllImg', data).then(res => {
        if (res.status == 1) {
          this.total = res.count
          this.pageList = res.list
          if (res.list.length == 0) {
            this.$message.error('图片库暂无素材~');
          }
        } else if (res.status == 2) {
          this.$router.push({
            name: 'login',
            path: '/login'
          });
        } else {
          this.$message.error('图发太多了，微信不让，明天再斗吧');
        }
      });
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.getChatList()
      this.$emit('activeIndex', 'customerService')
      if (sessionStorage.getItem("name")) {
        this.$emit('nowPublic', sessionStorage.getItem("name"))
      } else {
        this.$emit('nowPublic', '暂时没有哦')
      }

    });
  }
}

</script>
<style scoped>
.main {
  width: 100%;
  height: 100%
}

.customerService {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
}

.left {
  height: 100%;
  overflow: hidden;
}

.scrollbar {
  height: 100%;
  background: #eae8e7;
}

.item {
  padding: 10px;
  overflow: hidden;
  cursor: pointer;
}

.item:hover {
  background: #cacaca;
}

.item.active {
  background: #cacaca;
}

.imgurl {
  width: 45px;
  border-radius: 3px;
  float: left;
}

.el-badge {
  float: left;
}

.item-center {
  float: left;
  margin-left: 20px;
}

.item-center .name {
  font-size: 16px;
  margin-bottom: 6px;
  width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.item-center .lastquestion {
  color: #b9b9b9;
  width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.item_right {
  float: right;
  color: #999;
}

.item_right span {
  display: block;
}

.item_right img {
  width: 20px;
  float: right;
  margin-top: 7px;
}

.center_bottom {
  height: 160px;
  width: 100%;
}

.center_bottom .toolsBar {
  position: relative;
  height: 28px;
}

.center_bottom .toolsBar img {
  width: 18px;
  margin: 5px 0px 5px 20px;
  cursor: pointer;
}

.record .link_wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 9;
  background: #eee;
  padding: 10px 10px 0;
}

.record .label_wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 9;
  width: 300px;
  height: 250px;
  overflow: hidden;
}

.cascaderTwo {
  background: #eee;
  position: absolute;
  left: 300px;
  padding: 0 10px 10px 10px;
  top: 0px;
  width: 280px;
  transition: all 1s;
}

.cascaderTwo p {
  text-align: center;
  font-size: 16px;
}

.cascaderTwo img {
  position: absolute;
  left: 5px;
  top: 11px;
  width: 30px;
  cursor: pointer;
}

.label_add {
  width: 280px;
  margin-top: 10px;
}

.cascader {
  position: absolute;
  left: -290px;
  bottom: 0px;
  width: 280px;
  transition: all 1s;
  background: #eee;
  padding: 10px;
}

.label_wrap .active1 {
  left: 0px;
}

.label_wrap .active2 {
  left: 0px;
}

.center {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 1;
  overflow: hidden;
}

.center .center_center {
  flex: 1;
  overflow: hidden;
}

.record {
  height: 100%;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
}

.search {
  height: 100%;
  background: #eae8e7;
}

.left_Dialog {
  overflow: hidden;
}

.left_Dialog .img {
  width: 45px;
  float: left;
  border-radius: 3px;
  cursor: pointer;
}

.right_Dialog {
  overflow: hidden;
}

.right_Dialog .img {
  width: 45px;
  float: right;
  border-radius: 3px;
}

.send {
  position: relative;
  max-width: 70%;
  border-radius: 5px;
  padding: 0 10px;
}

.left_Dialog .send {
  float: left;
  margin-left: 10px;
  background: #fff;
}

.left_Dialog .img2_wrap {
  float: left;
  margin-left: 10px;
}

.img2 {
  width: 120px;
}

.right_Dialog .send {
  float: right;
  margin-right: 10px;
  background: #9eea6a;
}

.right_Dialog .img2_wrap {
  float: right;
  margin-right: 10px;
}

.send .arrow {
  position: absolute;
  top: 10px;
  width: 0;
  height: 0;
  font-size: 0;
  border: solid 10px;
}

.left_Dialog .send .arrow {
  left: -20px;
  border-color: rgba(250, 0, 255, 0) #ffffff rgba(250, 0, 255, 0) rgba(250, 0, 255, 0);
}

.right_Dialog .send .arrow {
  right: -20px;
  border-color: rgba(250, 0, 255, 0) rgba(250, 0, 255, 0) rgba(250, 0, 255, 0) #9eea6a;
}

.time_Dialog {
  text-align: center;
}

.time_Dialog span {
  background: #dadada;
  color: #fff;
  height: 22px;
  line-height: 22px;
  border-radius: 2px;
  padding: 0 8px;
  display: inline-block;
}

textarea {
  border: 0;
  outline: 0;
  width: 99%;
  height: 85px;
  display: block;
  margin: 0 auto;
  resize: none;
  line-height: 20px;
  font-size: 16px;
}

.noUser {
  font-size: 16px;
  text-align: center;
  margin-top: 30px;
}

.textarea_wrap {
  position: relative;
  height: 124px;
}

.tip_wrap {
  position: absolute;
  right: 5px;
  bottom: 28px;
}

.tip_img {
  width: 140px;
}

.tip_text {
  position: absolute;
  left: 0;
  top: 18px;
  width: 100%;
  text-align: center;
}

.button {
  position: absolute;
  right: 30px;
}

pre {
  white-space: pre-wrap!important;
  word-wrap: break-word!important;
  *white-space: normal!important;
  line-height: 20px;
}

.img2 {
  width: 118px;
  height: 118px;
  float: left;
  margin: 6px;
  border: 1px solid transparent
}

.specInput {
  border: 0;
  outline: none;
  width: 109px;
}

.userItem {

  margin-bottom: 6px;
  position: relative;
}

.userSave {
  color: #409EFF;
  text-decoration: underline;
  padding-left: 10px;
}

.headimgurl {
  width: 60px;
  border-radius: 5px;
  position: absolute;
  right: 0;
  top: -58px;
}

.inputTelBefore {
  width: 90%;
  margin: 100px auto;
}

.inputTelBefore .btn {
  display: block;
  margin: 30px auto;
}

.userRecord_item {
  height: 50px;
  padding: 0 0 0 20px;
  cursor: pointer;
  border-bottom:1px solid #ccc;
}

.userRecord_item .time {
  float: left;
  width: 16%;
  text-align: center;
  margin-top: 9px;
}

.userRecord_item .record {
  float: left;
  font-size: 15px;
  line-height: 50px;
  background: #eae8e7;
  overflow: hidden;
  max-width: 84%;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.inputTelAfter {
  height: 100%;
  position: relative;
}

.inputTelAfter .scroll {
  height: 100%;
}

.detailBox {
  position: absolute;
  border: 1px solid #ccc;
  width: 300px;
  height: 300px;
  top: 100px;
  left: -300px;
  background: #fff;
}

</style>



